body {
  background-color: rgb(250, 248, 239);
}
.App {
  background-color: rgb(216, 198, 178);
  min-height: 500px;
  width: 400px;
  margin: 40px auto;
}
#form input {
  border: 0;
  border-radius: 10px;
  width: 200px;
  height: 50px;
  padding: 0 20px;
  margin: 20px;
  font-size: 20px;
  font-family: "Lucida Sans", "Lucida Sans Regular", "Lucida Grande",
    "Lucida Sans Unicode", Geneva, Verdana, sans-serif;
}

#form button {
  height: 48px;
  width: 80px;
  border-radius: 5px;
  border: 1px;
}
